<template>
	<el-row :class="classes" id="banner">
		<el-col :span="24" class="banner-list">
			<div class="leftLogo" @click="gohome">
				<img src="../images/left_logo.png" alt="">
			</div>
			<ul :class="shadowClass">
				<li :class="[index === 0 ? 'cheeru-list-a' : '', index === -1 ? 'cheeru-list-b' : '']" class="menu"><a href="homepage.html" class="aitem"></a>首·页</li>
				<!-- <li :class="[index === 1 ? 'cheeru-list-b' : '']" class="menu"><a href="yudao.html" class="aitem"></a>遇·岛</li> -->
				<li :class="[index === 2 ? 'cheeru-list-b' : '']" class="menu"><a href="product.html" class="aitem"></a>产品商城</li>
				<li :class="[index === 3 ? 'cheeru-list-b' : '']" class="menu"><a href="group.html" class="aitem"></a>团建定制</li>
				<!-- <li :class="[index === 4 ? 'cheeru-list-b' : '']"><a href="service.html"></a>遇岛服务</li> -->
				<li class="user_name menu" v-if="islogin === true">
					<div class="user_photo">
						<img src="../images/user.jpg" alt="" v-if="haveAvatar === false">
						<img :src="imageurl" alt="" v-if="haveAvatar === true">
					</div>
					<span v-if="name === ''">暂无昵称</span>
					<span v-else>{{name}}</span>
					<ul class="submenu">
						<li class="subitem"><a href="personalaccount.html">个人中心</a></li>
						<li class="subitem" @click="logout">退出登录</li>
					</ul>
				</li>
				<li :class="[index === 5 ? 'cheeru-list-b' : '']" class="menu" v-else><a href="login.html" class="aitem"></a>登录/注册</li>
			</ul>
		</el-col>
	</el-row>
</template>

<script>
	import { getUserName } from '../../api/api'
	import { Vue } from 'js/base'
	import { oneOf } from 'js/utils'
	const prefixCls = 'cheeru-banner'
	export default {
		data () {
			return {
				imageurl: '',
				haveAvatar: true,
				temname: '',
				name: ''
			}
		},
		props: {
			type: {
				type: String,
				default: 'transparent',
				validator (value) {
					return oneOf(value, ['transparent', 'white']);
				}
			},
			index: {
				type: Number
			},
			shadow: {
				type: String
			}
		},
		watch: {
			temname() {
				this.name = this.temname;
			}
		},
		mounted() {
			if (sessionStorage.getItem(this.encryption('info'))) {
				this.default_accid = this.decrypt(JSON.parse(sessionStorage.getItem(this.encryption('info'))).www);
		  	this.default_token = this.decrypt(JSON.parse(sessionStorage.getItem(this.encryption('info'))).xxx);
		  	let info = {
	        account_id: this.default_accid,
	        token: this.default_token
	      };
	      getUserName(info).then((res) => {
	      	if (res.data.head_image === '') {
	      		this.haveAvatar = false;
	      	} else if (res.data.head_image !== '') {
	      		this.haveAvatar = true;
	      		this.imageurl = res.data.head_image;
	      	}
	        if (res.data.user_name === '') {
	          this.temname = res.data.other_user_name;
	        } else {
	          this.temname = res.data.user_name;
	        }
	      });
			}
		},
		created() {
			if (sessionStorage.getItem('name')) {
				this.name = sessionStorage.getItem('name');
				this.temname = this.name;
			} else {
				if (sessionStorage.getItem(this.encryption('info'))) {
					this.name = decodeURI(JSON.parse(sessionStorage.getItem(this.encryption('info'))).zzz);
				}
			}
		},
		methods: {
			gohome() {
				window.location.href = 'homepage.html';
			},
			logout() {
				this.$confirm('确认退出登录吗？', '提示', {
  				type: 'info'
  			}).then(() => {
  				window.location.href = 'homepage.html';
  				sessionStorage.removeItem(this.encryption('info'));
  				sessionStorage.removeItem('name');
  				if (this.getCookie('login_type') && this.getCookie('login_type') !== '') {
			        document.cookie = 'login_type=; expires=Session; path=/';
			        document.cookie = 'call_phone=; expires=Session; path=/';
			        document.cookie = 'token=; expires=Session; path=/';
			        document.cookie = 'user_id=; expires=Session; path=/';
			        document.cookie = 'user_name=; expires=Session; path=/';
			        document.cookie = 'userq_id=; expires=Session; path=/';
			        document.cookie = 'userw_id=; expires=Session; path=/';
			        document.cookie = 'other_user_name=; expires=Session; path=/';
  				}
  			}).catch(() => {
  			});
			},
			setCookie(name, value, expiredays) {
		      var exdate = new Date();
		      exdate.setDate(exdate.getDate() + expiredays);
		      document.cookie = name + '=' + escape(value) + ((expiredays === null) ? '' : '; expires=' + exdate.toGMTString());
		    },
			getCookie(name) {
		      if (document.cookie.length > 0) {
		        var start = document.cookie.indexOf(name + '=');
		        if (start !== -1) {
		        start = start + name.length + 1
		        var end = document.cookie.indexOf(';', start);
		        if (end === -1) end = document.cookie.length
		        return unescape(document.cookie.substring(start, end))
		        }
		        }
		        return ''
		    }
		},
		computed: {
			islogin() {
				let user = sessionStorage.getItem(this.encryption('info'));
				if (user) {
					return true;
				} else {
					return false;
				}
			},
			classes () {
				return [
					{
						[`${prefixCls}-${this.type}`]: this.type
					}
				]
			},
			shadowClass () {
				return [
					{
						[`${prefixCls}-${this.shadow}`]: this.shadow
					}
				]
			}
		}
	}
</script>

<style lang="less">
	.cheeru-banner-transparent{
		background-color: transparent;
		border-bottom:2px solid rgba(255,255,255,0.3);
	}
	.cheeru-banner-white{
		background-color: #fff;
		border-bottom:2px solid #E5E5E5;
	}
	.el-message-box{
		text-align: left;
		.el-message-box__header
			.el-message-box__headerbtn{
				background: #fff;
				border: none;
				i:hover{
					color: #999;
				}
			}
		.el-message-box__content
			.el-icon-information{
				color: #20e3b3;
			}
		.el-message-box__btns
			.el-message-box__headerbtn{
				background: #fff;
			}
			button:nth-child(1){
				border:1px solid #20e3b3;
				color: #20e3b3;
			}
			button:nth-child(2){
				border:1px solid #20e3b3;
				color:#fff;
				background: #20e3b3;
			}
	}
	#banner{
		min-width: 1200px;
		position: fixed;
		top:0;
		width: 100%;
		z-index:999;
		.banner-list{
			.leftLogo{
				float: left;
				cursor: pointer;
				margin-left: 40px;
				margin-top: 14px;
				img{
					height: 48px;
				}
			}
			ul {
				float:right;
				.menu {
					margin-right: 70px;
					padding-top: 24px;
					padding-bottom: 25px;
					cursor: pointer;
					float:left;
					font-size: 17px;
					position:relative;
					top:2px;
					color:#3d4947;
					.aitem{
						position:absolute;
						top:0;
						width: 100%;
						height: 100%;
					}
					&.user_name{
						cursor: pointer;
						padding-top: 1px;
						position: relative;
						&:hover
							.submenu{
								display: block;
							}
						.submenu{
							display: none;
							position: absolute;
							width: 120%;
							bottom: -120px;
							background-color: #fff;
							border:1px solid #e3e3e3;
							border-top: none;
							border-bottom: none;
							left: 0;
							box-sizing: border-box;
							.subitem{
								width: 100%;
								height: 60px;
								color:#888888;
								text-align: center;
								line-height: 60px;
								font-size: 16px;
								border-bottom: 1px solid #e3e3e3;
								box-sizing: border-box;
								a{
									display: block;
									text-decoration: none;
									color:#888;
								}
							}
						}
						.user_photo{
							width: 42px;
							height: 42px;
							position: relative;
							display: inline-block;
							top:13px;
							border-radius: 50%;
							img{
								width: 42px;
								height: 42px;
								border-radius: 50%;
							}
						}
						span{
							margin-left: 10px;
							color:#888888;
						}
					}
				}
			}
			.cheeru-banner-shadow {
				li {
					text-shadow:0px 0px 1px #262626;
				}
			}
			.cheeru-list-a{
				color:#fff;
				border-bottom:2px solid #fff;
			}
			.cheeru-list-b{
				color:#000;
				border-bottom:2px solid #72B9DE;
			}
		}
	}
</style>



