<script>
import banner from '../../components/common/banner'
import footer from '../../components/common/footer'
import { getRoutePlainFare, getDescAllInfo, updateOrder } from '../../api/api'
export default {
	data() {
		return {
			loading: true,
			successclick: true,
			adult: '',
			children: '',
			rooms: '',
			date: '',
			background_img: '',
			desc_info: [],
			routeName: '',
			routeDes: '',
			cost_includes: '',
			free_item: '',
			cost_except: '',
			agreement: '',
			hint: '',
			status: '',
			routeDays: [],
			route_info: [],
			showcal: false,
			headshow: true,
			currentIndex: 0,
			tabArray: ['概述', '详细', '安排'],
	      	test: [],
	      	currentDay: 1,
	      	selectindex: '',
	      	currentMonth: 1,
	     	currentYear: 1970,
	      	currentWeek: 1,
	     	days: [],
	     	activeNames: ['2'],
			pickerOptions0: {
	        disabledDate(time) {
	          return time.getTime() < Date.now() - 8.64e7;
	        }
	      }
		}
	},
	methods: {
		checkcorrect() {
			// this.successclick = false;
		},
		checkform() {
			if (this.date === '') {
				this.$message('请选择出行日期');
			} else if (this.adult === 0 && this.children === 0) {
				this.$message('请输入出行人数');
			} else if (this.rooms === 0) {
				this.$message('请输入房间数量');
			} else {
				let info = {
					order_name: this.route_info.route_info.route_name,
					route_id: this.route_info.route_info.id,
					trip_date: this.date,
					room_number: this.rooms,
					adult_number: this.adult,
					child_number: this.children,
					adult_price: 1000,
					child_price: 500
				};
				if (sessionStorage.getItem(this.encryption('temp_id'))) {
					info.temp_id = this.decrypt(sessionStorage.getItem(this.encryption('temp_id')));
				}
				if (sessionStorage.getItem(this.encryption('temp_route_id'))) {
					info.temp_route_id = this.decrypt(sessionStorage.getItem(this.encryption('temp_route_id')));
				}
				info.update_total_price = info.adult_price * info.adult_number + info.child_price * info.child_number;
				if (sessionStorage.getItem(this.encryption('info'))) {
					info.create_order_people_id = this.decrypt(JSON.parse(sessionStorage.getItem(this.encryption('info'))).www);
				}
				let info2 = {'order_info': info};
				updateOrder(info2).then((res) => {
					if (typeof (res.data) === 'number') {
						sessionStorage.setItem(this.encryption('order'), this.encryption(res.data));
						window.location.href = 'paystep.html';
					}
				});
			}
		},
		booktrip() {
			if (this.getCookie('login_type') !== '') {
				this.checkform();
			} else {
				let cookie = sessionStorage.getItem(this.encryption('info'));
				if (!cookie) {
					this.$message('请先登录');
				} else {
					this.checkform();
				}
			}
		},
		closedate() {
			this.showcal = false;
		},
		getCookie(name) {
	      if (document.cookie.length > 0) {
	        var start = document.cookie.indexOf(name + '=');
	        if (start !== -1) {
	        start = start + name.length + 1
	        var end = document.cookie.indexOf(';', start);
	        if (end === -1) end = document.cookie.length
	        return unescape(document.cookie.substring(start, end))
	        }
	        }
	        return ''
	    },
		showit() {
			this.showcal = true;
		},
		stopclick() {
		},
		initData: function(cur) {
	      var date;
	      if (cur) {
	          date = new Date(cur);
	      } else {
	          date = new Date();
	      }
	      this.currentDay = date.getDate();
	      this.currentYear = date.getFullYear();
	      this.currentMonth = date.getMonth() + 1;
	      this.currentWeek = date.getDay(); // 1...6,0
	      if (this.currentWeek === 0) {
	          this.currentWeek = 7;
	      }
	      var str = this.formatDate(this.currentYear, this.currentMonth, this.currentDay);
	      // console.log('today:' + str + ',' + this.currentWeek);
	      this.days.length = 0;
	      // 今天是周日，放在第一行第7个位置，前面6个
	      for (let i = this.currentWeek - 1; i >= 0; i--) {
	          let d = new Date(str);
	          d.setDate(d.getDate() - i);
	          // console.log('y:' + d.getDate());
	          this.days.push(d);
	      }
	      for (let i = 1; i <= 42 - this.currentWeek; i++) {
	          let d = new Date(str);
	          d.setDate(d.getDate() + i);
	          this.days.push(d);
	      }
	  	},
			pickPre: function(year, month) {
	        this.selectindex = '';
	        this.temstatus = '';
	        var d = new Date(this.formatDate(year, month, 1));
	        d.setDate(0);
	        this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));
	    },
	    pickNext: function(year, month) {
	    	this.selectindex = '';
	    	this.temstatus = '';
	        var d = new Date(this.formatDate(year, month, 1));
	        d.setDate(42);
	        this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));
	    },
	    pickYear: function(year, month) {
	        alert(year + ',' + month);
	    },
	    formatDate: function(year, month, day) {
	        var y = year;
	        var m = month;
	        if (m < 10) m = '0' + m;
	        var d = day;
	        if (d < 10) d = '0' + d;
	        return y + '-' + m + '-' + d;
	    },
	    formatDate2(date) {
	      var y = date.getFullYear();
	      var m = date.getMonth() + 1;
	      m = m < 10 ? '0' + m : m;
	      var d = date.getDate();
	      d = d < 10 ? ('0' + d) : d;
	      return y + '-' + m + '-' + d;
	    },
    pick(data, index) {
    	let el = this.$refs['li-' + index][0];
    	if (data.getMonth() + 1 !== this.currentMonth || el.children.length !== 2 || this.test[0][this.formatDate2(data)].allow === false) {
    		return;
    	} else {
    		this.date = this.formatDate2(data);
    		this.selectindex = index;
    		this.showcal = false;
    	}
    },
    formdate(Time) {
    	let y = Time.getFullYear();
      let m = Time.getMonth() + 1;
      let d = Time.getDate();
      if (m < 10) { m = '0' + m; }
      if (d < 10) { d = '0' + d; }
      return y + '-' + m + '-' + d;
    },
		gonum(item, index) {
			this.currentIndex = index;
			if (index === 0) {
				window.scrollTo(0, document.getElementById('routeName').offsetTop + 118);
			} else if (index === 1) {
				window.scrollTo(0, document.getElementById('detail').offsetTop + 560);
			} else if (index === 2) {
				window.scrollTo(0, document.getElementById('arrange').offsetTop + 620);
			}
		},
		scrollEvent() {
			let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
			if (scrollTop > document.body.offsetHeight - 100) {
				this.headshow = false;
			} else {
				this.headshow = true;
			}
		}
	},
	created() {
		if (sessionStorage.getItem('routeId')) {

		} else {
			window.location.href = 'product.html';
		}
		this.initData(null);
		let info = {
  		route_id: sessionStorage.getItem('routeId')
		};
    getRoutePlainFare(info).then((res) => {
        this.test = res.data.msg;
    });
    getDescAllInfo(info).then((res) => {
    	this.route_info = res.data;
    	if (this.route_info.route_info) {
    		if (this.route_info.route_info.route_image.length !== 0) {
    			this.background_img = 'http://price.cheeruislands.com/uploads/file/' + this.route_info.route_info.route_image[0].image_path;
    		}
    		this.routeName = this.route_info.route_info.route_name;
    		this.routeDes = this.route_info.route_info.route_describe;
    		this.status = this.route_info.route_info.route_type;
    		this.cost_includes = this.route_info.note_info.cost_includes;
    		this.cost_except = this.route_info.note_info.cost_except;
    		this.free_item = this.route_info.note_info.free_item;
    		this.agreement = this.route_info.note_info.agreement;
    		this.hint = this.route_info.note_info.hint;
    		this.desc_info = res.data.desc_info;
    		for (let arr in this.route_info) {
    			if (arr !== 'desc_info' && arr !== 'note_info' && arr !== 'route_info') {
    				this.routeDays.push(this.route_info[arr]);
    			}
    		}
    	}
    });
	},
	mounted() {
		window.onscroll = () => {
			let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
			this.currentIndex = 0;
			if ((scrollTop > document.getElementById('routeName').offsetTop + 118) && (scrollTop < document.getElementById('arrange').offsetTop + 600)) {
				this.currentIndex = 1;
			} else if ((scrollTop > document.getElementById('arrange').offsetTop + 600)) {
				this.currentIndex = 2;
			}
			// && (scrollTop < document.getElementById('comment').offsetTop)
			//  else if (scrollTop > document.getElementById('comment').offsetTop) {
			// 	this.currentIndex = 3;
			// }
		};
		document.getElementById('header').style.height = (document.body.offsetHeight - 192) + 'px';
		window.onresize = () => {
			document.getElementById('header').style.height = (document.body.offsetHeight - 192) + 'px';
		};
		window.addEventListener('scroll', this.scrollEvent);
	},
	components: {
		'cheeru-banner': banner,
		'cheeru-footer': footer
	}
}
</script>

